$(function () {
    const $window = $(window);
    const $comparisonFixed = $('.js-comparison-fixed');
    const $comparisonNav = $('.js-comparison-nav');
    const $imgComparisonSlider = $('.js-comparison-img-slider');
    const $comparisonSlider = $('.js-comparison-slider');
    const $comparisonParamSlider = $('.js-comparison-params-slider');
    
    const $comparisonSliderClone = $('.js-comparison-slider-clone');
    const $comparisonParamSliderClone = $('.js-comparison-params-slider-clone');
    const $comparisonCounterNav = $('.js-comparison-counter-nav');
    const $comparisonCounterNavClone = $('.js-comparison-counter-nav-clone');
    const addZero = (n)=> `${n < 10 ? '0'+n : n}`
    const counterTemplate = (count, total)=> {
        return `
            <span class="active">${count}</span> <span class="total">/ ${total}</span>
        `
    }

    $comparisonNav.on('click', 'button', (e)=> {
        const { dir } = e.currentTarget.dataset
        if (dir === 'prev') $comparisonSlider.slick('slickPrev')
        if (dir === 'next') $comparisonSlider.slick('slickNext')    
    })

    $comparisonCounterNav.on('click', 'button', (e)=> {
        const { dir } = e.currentTarget.dataset
        if (dir === 'counter-prev') $comparisonSlider.slick('slickPrev')
        if (dir === 'counter-next') $comparisonSlider.slick('slickNext')   
    })

    $comparisonCounterNavClone.on('click', 'button', (e)=> {
        const { dir } = e.currentTarget.dataset
        if (dir === 'counter-clone-prev') $comparisonSliderClone.slick('slickPrev')
        if (dir === 'counter-clone-next') $comparisonSliderClone.slick('slickNext')   
    })


    $comparisonSlider.on('init reInit afterChange', function(event, slick, currentSlide, nextSlide) {
        let i = (currentSlide ? currentSlide : 0) + 1;
        slick.$slider.parent('[data-catalog]').find('.js-comparison-counter').html(counterTemplate(addZero(i), addZero(slick.slideCount)));
        if (slick.slideCount === 0) {
            $('.js-comparison-counter').css("display", "none")
            $('.js-comparison-wrap').remove()
        }
    })

  
    $comparisonSliderClone.on('init reInit', function(event, slick, currentSlide, nextSlide){
        slick.$slider.parent('[data-catalog]').find('.js-comparison-counter-clone').html(counterTemplate('02', addZero(slick.slideCount)));
    });

    $comparisonSliderClone.on('init afterChange', function(event, slick, currentSlide, nextSlide){

        var i = (currentSlide ? currentSlide : 0) + 1;
        slick.$slider.parent('[data-catalog]').find('.js-comparison-counter-clone').html(counterTemplate(addZero(i), addZero(slick.slideCount)));

        if (slick.slideCount === 0) {
            slick.$slider.parent('[data-catalog]').find('.js-comparison-counter-clone').css("display", "none")
        }
    });
 

    $comparisonSlider.slick({
        infinite: false,
        slidesToShow: 3,
        slidesToScroll: 3,
        asNavFor: '.js-comparison-params-slider',
        arrows: false,
        dots: false,

        responsive: [
            {
                breakpoint: 1381,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                    arrows: false,
                    dots: false,
                }
            },

            {
                breakpoint: 1025,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    arrows: false,
                    dots: false,
                }
            }
        ]
    });

    $comparisonParamSlider.slick({
        infinite: false,
        slidesToShow: 3,
        slidesToScroll: 1,
        asNavFor: '.js-comparison-slider',
        arrows: false,
        dots: false,
        draggable: false,
        touchMove: false,
        swipe: false,
        
        responsive: [
            {
                breakpoint: 1381,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    arrows: false,
                    dots: false,
                    draggable: false,
                    touchMove: false,
                    swipe: false,
                }
            },

            {
                breakpoint: 1025,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    arrows: false,
                    dots: false,
                    draggable: false,
                    touchMove: false,
                    swipe: false,
                }
            }


        ]
    })


    $comparisonSliderClone.slick({
        infinite: false,
        slidesToShow: 1,
        slidesToScroll: 1,
        asNavFor: '.js-comparison-params-slider-clone',
        arrows: false,
        dots: false,
    });

    $comparisonParamSliderClone.slick({
        infinite: false,
        slidesToShow: 1,
        slidesToScroll: 1,
        asNavFor: '.js-comparison-slider-clone',
        arrows: false,
        dots: false,
        draggable: false,
        touchMove: false,
        swipe: false,
        
    });

    $window.on('scroll', ()=> {
        if($window.scrollTop() >= 470) {
            $comparisonFixed.addClass("is_fixed");
        } else {
    
            $comparisonFixed.removeClass("is_fixed");
        }
    })




    $imgComparisonSlider.slick({
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: false,
        dots: true,
    });

});