$(function() {
    const $sliderProduct = $('.js-product-slider');
    const $sliderProductNav = $('.js-product-nav');
    const $sliderProductNavPrev = $('.js-product-nav-prev');
    const $sliderProductNavNext = $('.js-product-nav-next');

    if(!$sliderProduct.length) return;

    $sliderProduct.slick({
        slidesToShow: 1,
        slidesToScroll: 1,
        dots: false,
        arrows: false,
        asNavFor: '.js-product-nav',
        adaptiveHeight: true,

        responsive: [

            {
                breakpoint: 1025,
                settings: {
                    asNavFor: null,
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    dots: true,
                    arrows: false,
                }
            },
        
        ]

    })

    $sliderProductNav.slick({
        slidesToShow: 3,
        slidesToScroll: 1,
        dots: false,
        arrows: false,
        asNavFor: '.js-product-slider',
        focusOnSelect: true,
        verticalSwiping: true,
        // variableWidth: true,
        vertical: true,
        // centerMode: true,
    });

    $sliderProductNavPrev.on('click', ()=> $sliderProduct.slick('slickPrev'));
    $sliderProductNavNext.on('click', ()=> $sliderProduct.slick('slickNext'));

});