$(function () {
    const $cardSlider = $('.js-card-slider');

    if(!$cardSlider.length) return;

    $cardSlider.each((_, slider) => {
        const $slider = $(slider);

        $slider.slick({
            slidesToShow: 1,
            slidesToScroll: 1,
            arrows: false,
            dots: true,
        });
        
    })

});