$(()=> {
    const $tabBtn = $('.js-tabs-btn');
    const $tabContent = $('.js-tabs-content');

    if ($tabBtn.length) {
        $tabBtn.on('click', function() {
            $tabBtn.toggleClass('tabs__button_active');
            const $that = $(this);
            const tab = $that.data('tab');
            $tabContent.children().removeClass('is-active')
            $tabContent.find(`[data-tab="${tab}"]`).addClass('is-active')
        })
    }
})