$(function() {
    const $sliderImg = $('.js-slider-img');
    const $sliderImgPrev = $('.js-slider-img-prev');
    const $sliderImgNext = $('.js-slider-img-next');

    if(!$sliderImg.length) return;

    $sliderImg.slick({
        slidesToShow: 3,
        slidesToScroll: 1,
        dots: false,
        arrows: false,

        responsive: [
            {
                breakpoint: 1240,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    dots: false,
                    arrows: false,

                }
            },

            {
                breakpoint: 670,
                settings: {
                    dots: false,
                    arrows: false,
                     variableWidth: true,
                }
            },
        
        ]
    });
        

    $sliderImgPrev.on('click', ()=> $sliderImg.slick('slickPrev'));
    $sliderImgNext.on('click', ()=> $sliderImg.slick('slickNext'));
 
});