$(()=> {
    const $jsFancy = $('.js-fancy');

    $jsFancy.on('click', e=> {
        e.preventDefault();
        const modalSrc = e.currentTarget.dataset.src || e.currentTarget.getAttribute('data-src')

        showFancyBox(modalSrc) 
    })

    function showFancyBox(src) {
        Fancybox.show([
            {
                src,
                type: "inline",
            }
        ])
    }

    window.showFancyBox = showFancyBox;
})