$(() => {
    const $anchor = $('.js-anchor-form');

    $anchor.on('click', e=> {

        e.preventDefault();
        const $hash = e.currentTarget.getAttribute('href');
        const scrollToBlock = document.querySelector($hash)
            window.scrollTo({ 
                behavior: "smooth",
                top: scrollToBlock.offsetTop - 50,
            })
    })
})