$(()=> {
    const app = {
        numberFormat (num) {
            return num.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, " ")
        },

        counter (selector) {
            const $app = typeof selector === 'string' ? document.querySelector(selector) : selector 
            if ($app) {
                listenApp = this.changeAllSumOutputResult($app)

                const { min, max, price } = $app.dataset
                const decrement = $app.querySelector('[data-decrement]')
                const increment = $app.querySelector('[data-increment]')
                const input = $app.querySelector('[data-input]')
                const sumResult = $app.querySelector('[data-sum]')
                let counter = Number(input.value)
    
                const changeSum = (counter)=>  {
                    const sum = Number(price) * counter 
                    sumResult.innerText = this.numberFormat(sum)
                    sumResult.setAttribute('data-sum', sum)
                }
    
                changeSum(counter)
          
    
                decrement.addEventListener('click', ()=> {
                    if (Number(min) < counter) {
                        counter--
                        input.value = counter
    
                        changeSum(counter)
                        listenApp()
                    }
                })
    
                increment.addEventListener('click', ()=> {
                    if (Number(max) > counter) {
                        counter++
                        input.value = counter
    
                        changeSum(counter)
                        listenApp()
                    }
                })
    
                input.addEventListener('change', (e)=> {
                    const value = e.target.value
                    if (value === '') e.target.value = counter = Number(min)
                    if (Number(value) < Number(max)) counter = Number(value)
                    else e.target.value = counter = Number(max)
    
                    changeSum(counter)
                    listenApp()
                })
            }
        },

        handleSumChange() {
            let sum = 0
            Array.from(document.querySelectorAll('.js-counter [data-sum]')).forEach(s=>{
                sum += +s.dataset.sum
            })
            const shoppingSum = document.querySelector('.js-shopping-sum')
            if (shoppingSum) shoppingSum.innerText = app.numberFormat(sum)
        },

        changeAllSumOutputResult(el) {
            const event = new Event('changeSum')
            el.addEventListener('changeSum', this.handleSumChange, false)
            return ()=> el.dispatchEvent(event)
        }
    }

    document.querySelectorAll('.js-counter').forEach(el => app.counter(el))
    app.handleSumChange()
})
