$(function() {
    const $sliderCatalog = $('.js-catalog-slider');
    const $sliderCatalogPrev = $('.js-catalog-slider-prev');
    const $sliderCatalogNext = $('.js-catalog-slider-next');

    if(!$sliderCatalog.length) return;

    $sliderCatalog.slick({
        slidesToShow: 1,
        slidesToScroll: 1,
        dots: false,
        arrows: false,
    });
        

    $sliderCatalogPrev.on('click', ()=> $sliderCatalog.slick('slickPrev'));
    $sliderCatalogNext.on('click', ()=> $sliderCatalog.slick('slickNext'));
 
});