$(function() {
    const $sliderVideo = $('.js-slider-video');
    const $sliderVideoPrev = $('.js-slider-video-prev');
    const $sliderVideoNext = $('.js-slider-video-next');

    if(!$sliderVideo.length) return;

    $sliderVideo.slick({
        slidesToShow: 1.5,
        slidesToScroll: 1,
        dots: false,
        arrows: false,
        // variableWidth: true,

        responsive: [
            {
                breakpoint: 1025,
                settings: {
                    slidesToShow: 1.2,
                    slidesToScroll: 1,
                    dots: false,
                    arrows: false,
                },
            },
        ]
    });
        
    $sliderVideo.on('afterChange', ()=> {
        $sliderVideo.find('video').each((_, video) => {
            video.pause();
        })
    })

    $sliderVideoPrev.on('click', ()=> $sliderVideo.slick('slickPrev'));
    $sliderVideoNext.on('click', ()=> $sliderVideo.slick('slickNext'));



    $sliderVideo.on('click', '.js-video', function () {
        const that = $(this)
        that.addClass('active')
        const video = that.find('video')
        video.attr('controls', true)
        video[0].play()
    })
    
});

