
$(function() {
    const mapAddress = [
        {
            id: 1,
            coords: [55.804691, 36.981356],
            city: "Москва",
        },
    
        {
            id: 2,
            coords: [55.602142, 37.346512],
            city: "Санкт-Петербург",
        },
    
        {
            id: 3,
            coords: [55.909983, 37.736710],
            city: "Пермь",
        },
    
        {
            id: 4,
            coords: [55.570397, 37.475477],
            city: "Владимир",
        }
    ]

    const $ymapsObj = $('.buy');
    const $buyShops = $('.js-buy-shops');
    const $buyShopSearch = $('.js-shop-search');
    if(!$ymapsObj.length) return;

    let myMap = null;
    let markers = []
    ymaps.ready(init);
    function init() {
        $('.buy__map').each((_, el) => {
            myMap = new ymaps.Map(el, {
                center: [55.814472, 37.324874],
                zoom: 8,
                controls: []
            });
        });
        
        setPlaceMarks(mapAddress, (mapPlace)=> myMap.geoObjects.add(mapPlace))
    }
 
    function setPlaceMarks(data, cb) {
        const addresses = data.length > 0 ? data : [data]
        addresses.forEach((address) => {
            const mapPlace = new ymaps.Placemark(address.coords, {

            }, {
                iconLayout: 'default#image',
                iconImageHref: './img/content/pin.png',
                iconColor: '#5A5C69',
                iconImageSize: [24, 46],
                imageOffset: [-21, -56],
            })
            mapPlace.options.set('_id_', address.id)
            markers.push(mapPlace)

            mapPlace.events.add('click', e=> {
                const options = e.get('target').options
                const currentId = options.get('_id_')
                changeShops(currentId)
                changePlacemark(currentId)
            })

            cb(mapPlace)
        })
    }

    function changeShops(id) {
        const scrollTo = $(`[data-shop-id="${id}"]`)
        scrollTo.toggleClass('is_active').siblings().removeClass('is_active')
        const parent = scrollTo.parent()
        const position = scrollTo.offset().top - parent.offset().top + parent.scrollTop();
        parent.animate({ scrollTop: position })
    }

    function changePlacemark(id) {
        const currentPin = markers.find(p=> p.options.get('_id_') === Number(id))

        markers.filter(p => p.options.get('_id_') !== Number(id)).forEach(p => {
            if (p.options.get('iconImageHref').includes('pin-orange')) {
                p.options.set('iconImageHref', './img/content/pin.png');
            }
        })
        
        if (!currentPin.options.get('iconImageHref').includes('pin-orange')) {
            currentPin.options.set('iconImageHref', './img/content/pin-orange.png');
        } else {
            currentPin.options.set('iconImageHref', './img/content/pin.png');
        }
    }

    function shopsFilter(value) {
        const filter = value.toUpperCase()
        const items = $buyShops.find('[data-shop-city]')

        items.each((_, item) => {
            item.classList.remove('is_active')
            const { shopCity } = item.dataset
            
            if (shopCity.toUpperCase().indexOf(filter) > -1) {
                item.style.display = null
            } else {
                item.style.display = 'none'
            }
        })
    }

    let timer = null
    $buyShopSearch.on('input', e=> {
        clearTimeout(timer)
        timer = setTimeout(()=> {
            markers.forEach(p => myMap.geoObjects.remove(p))
            markers = []
            const value = e.target.value
            const current = mapAddress.find(address => address.city.toLowerCase() === value.toLowerCase())
            shopsFilter(value)
            if (current) {
                setPlaceMarks(current, (mapPlace)=> myMap.geoObjects.add(mapPlace))
            } else if (value === '') {
                setPlaceMarks(mapAddress, (mapPlace)=> myMap.geoObjects.add(mapPlace))
            }
            clearTimeout(timer)
        }, 300)
    })

    $buyShops.on('click', '[data-shop-id]', e=> {
        const { dataset: { shopId } } = e.currentTarget
        changePlacemark(shopId)
        changeShops(shopId)
    })
})