$(function() {
    const $technologiesNav = $('.js-technologies-nav');
    const $sliderFor = $('.js-slider-for');
    const $sliderPrev = $('.js-slider-prev');
    const $sliderNext = $('.js-slider-next');

    if(!$sliderFor.length) return;

  
    $sliderFor.slick({
        slidesToShow: 1,
        slidesToScroll: 1,
        dots: false,
        arrows: false,
        speed: 500,
        fade: true,
        cssEase: 'linear',
        adaptiveHeight: true
    })

    $sliderPrev.on('click', ()=> $sliderFor.slick('slickPrev'));
    $sliderNext.on('click', ()=> $sliderFor.slick('slickNext'));

    $sliderFor.on('beforeChange', (e, slick, prev, next)=> {
        const current = $technologiesNav.children().eq(next)
        current.addClass('is_active').siblings().removeClass('is_active')

        scrollToPosition(current, $technologiesNav)
    })

    $technologiesNav.children().on('click', e => {
        const that = $(e.currentTarget)
        const idx = that.index()
        $sliderFor.slick('slickGoTo', idx)
    })

    function scrollToPosition(scrollTo, parent) {
        const position = scrollTo.offset().left - parent.offset().left + parent.scrollLeft();
        parent.animate({ scrollLeft: position })
    }
    
});


