
$(function()  {
    $('.js-phone').inputmask({"mask": "+7 (999) 999-9999"});

    const $addUrlInput = $('.js-add-url-input')
    
    $addUrlInput .on('click', '[data-add-button]', e=> {
        const $that = $(e.currentTarget)
        const parent = $that.parents('.js-add-url-input')
        const inputTemplate = parent.find('#add-url-input')[0].content.cloneNode(true)
        $that.parent().before(inputTemplate)

        const inputLength = $('[data-add-input]').length
        if (inputLength >= 4) {
            return  $that.remove()
        }
    })
    const submitHandler = ()=> {
        $.ajax('https://jsonplaceholder.typicode.com/todos')
            .done(()=> {
                window?.showFancyBox("#modal-success")
            })
    }

    const validationRules = {
        dealers: {
            rules: {
                lastname: "required",
                name: "required",
                surname: "required",

                job: "required",
                country: "required",
                locality: "required",

                region: "required",
                namecompany: "required",
                company: "required",
                sitecompany: {
                    required: true
                },

                email: {
                    required: true,
                    email: true,
                },
                phone: {
                    required: (elem)=> {
                        const onlyNumbers = elem.value.replace(/[^0-9]/g, '')
                        return onlyNumbers.length >= 11 ? false : true
                    }
                },
                url: {
                    required: true,
                }
            },

            messages: {
                name: "Указана неверная информация" ,
                surname: "Указана неверная информация",
                lastname: "Указана неверная информация",

                job: "Указана неверная информация",                  
                region: "Указана неверная информация",
                country: "Указана неверная информация",                
                locality: "Указана неверная информация",

                company: "Указана неверная информация",
                namecompany:  "Указана неверная информация",
                sitecompany: "Указана неверная информация",

                email: "Указана неверная информация",                
                phone: "Указана неверная информация",
                url: "Указана неверная информация",
            },

            submitHandler: function() {
                submitHandler()
                return false;
            }
        },
        shopping: {
            rules: {
                email: {
                    required: true,
                    email: true,
                },
                phone: {
                    required: (elem)=> {
                        const onlyNumbers = elem.value.replace(/[^0-9]/g, '')
                        return onlyNumbers.length >= 11 ? false : true
                    }
                },

                name: "required",

                city: {
                    required: true,
                },
                dealership: {
                    required: true,
                },

            },

            messages: {
                name: "Указана неверная информация" ,
                email: "Указана неверная информация",                
                phone: "Указана неверная информация",

                city:  "",
                dealership:  "",
            },

            submitHandler: function(form) {
                return false;
            }
        }
    }

    const $form = $('.js-form');

    if(!$form.length) return; {
        $form.each((_, form)=> {
            const { formValidation } = form.dataset            
            $(form).validate(validationRules[formValidation])
        })
    };

    
})


