$(()=> {
    const $tabs = $('.js-card-detail-tabs')
    const cardDetails =  $('.js-card-detail').children()

    $tabs.on('click', 'button[data-id]', e=> {
        const { id } = e.currentTarget.dataset
        const $current = $(e.currentTarget)
        changeClass($current)

        $('html, body').animate({
            scrollTop: $(`#${id}`).offset().top - 90
        }, 700);
    })

    function changeClass (btn) {
        btn.addClass('tabs__button_active').parent().siblings().children().removeClass('tabs__button_active')
    }

    function scrollToPosition(scrollTo, parent) {
        const position = scrollTo.offset().left - parent.offset().left + parent.scrollLeft();
        parent.animate({ scrollLeft: position })
    }
    
    let currentId = null

    $(window).on('scroll', ()=> {
        const scrollTop = $(window).scrollTop()

        cardDetails.each((_, block) => {
            const $block = $(block)
            const offsetTop = $block.offset().top - 100
            const offsetBottom = offsetTop + $block.outerHeight()

            if ((offsetTop <= scrollTop) && (offsetBottom >= scrollTop)) {
                if (currentId !== $block.attr('id')) {
                    currentId = $block.attr('id')
                    const $currentTab = $(`[data-id="${$block.attr('id')}"]`)
                    changeClass($currentTab)
                    scrollToPosition($currentTab, $tabs)
                }
            }
        });
    })
})